import {header} from "@/utils/DataTableUtil";
import {searchEventName, searchFriendConf, searchHero, searchItem} from "@/utils/SourceDataUtil";
import {itemType} from "@/utils/TypeValueConverter";
import {getEventCondition, isEventKe} from "@/model/event-condition";

const friendFilter = (value, select) => {
    return {
        type: 'select',
        label: '',
        value: value,
        selected: select,
        filterFunc: (value, item) => {
            if (value == '') {
                return true;
            }
            const friend = searchFriendConf(item.index);
            if (friend) {
                if (friend.group == value) {
                    return true
                }
            } else {
                const hero = searchHero(item.index);
                if (hero['ke']) {
                    return value == '';
                }
                else {
                    return value == 'NPC'
                }
            }
            return false
        }
    }
}
const typeFilter = (value, select) => {
    return {
        type: 'select',
        label: '',
        value: value,
        selected: select,
        filterFunc: (value, item) => {
            if (value == '') {
                return true;
            }
            return item.op == value;
        }
    }
}

const keFilter = {
    type: 'select',
    label: '',
    value: [ '', '', '' ],
    selected: '',
    filterFunc: (value, item) => {
        if (value == '') {
            return true;
        }
        if (value == '') {
            return item.ke
        } else {
            return !item.ke
        }
    }
}

