export const keEvents = [
    3247, //
    3318, 3319, 3320, 3321, 3322, 3323    // 
]

export const keEventRole = {
    3247: [956, 964],
}

export function isEventKe(eventId, condIdx) {
    let eventKe = keEvents.includes(eventId);
    const conditionList = getConditionList(eventId, condIdx)
    if (!conditionList) {
        return [eventKe, null]
    }
    for (let i = 0; i < conditionList.length; i++) {
        const condition = conditionList[i];
        // codeparamssymbol
        const reg = /(\w+)\((.*)\)(!*)/;
        const match = reg.exec(condition);
        const code = match[1];
        const params = match[2].split(',');
        const symbol = match[3];
        const isTrue = symbol.length === 0;
        // 36
        if (code == '36') {
            const variable = params[0];
            // 0-0-122
            if(variable == '0-0-122') {
                const value = params[2];
                const hero = searchHero(value);
                if (isTrue && hero) {
                    return [hero.ke || eventKe, hero.id]
                }
            }
        }
    }
    return [eventKe, null]
}

function getConditionList(eventId, condIdx) {
    if (condIdx == 0) {
        return null
    }
    const conditions = searchEventConditions(eventId);
    if (!conditions || conditions.length === 0) {
        return null;
    }
    const condition = conditions[condIdx];
    if (!condition) {
        return null;
    }
    const elements = condition.split(';');
    return elements;
}


export function getEventCondition(eventId, condIdx) {
    // condition;
    // ${code}(${params...})${symbol}
    // ${code}${params...}(,)${symbol}!()
    const conditionList = getConditionList(eventId, condIdx)
    if (!conditionList) {
        return null
    }
    return conditionList.map(parseSingleCondition);
}

function parseSingleCondition(condition) {
    // codeparamssymbol
    const reg = /(\w+)\((.*)\)(!*)/;
    const match = reg.exec(condition);
    if (!match) {
        return null;
    }
    const code = match[1];
    const params = match[2].split(',');
    const symbol = match[3];
    const isTrue = symbol.length === 0;
    //codeswitch
    switch (code) {
        case '4': return cond4Format(params, isTrue);
        case '5': return cond5Format(params, isTrue);
        case '6': return cond6Format(params, isTrue);
        case '9': return cond9Format(params, isTrue);
        case '11': return cond11Format(params, isTrue);
        case '16': return cond16Format(params, isTrue);
        case '18': return cond18Format(params, isTrue);
        case '20': return cond20Format(params, isTrue);
        case '28': return cond28Format(params, isTrue);
        case '29': return cond29Format(params, isTrue);
        case '31': return cond31Format(params, isTrue);
        case '36': return cond36Format(params, isTrue);
        case '42': return cond42Format(params, isTrue);
        case '43': return cond43Format(params, isTrue);
        case '60': return cond60Format(params, isTrue);
        case '72': return cond72Format(params, isTrue);
        case '75': return cond75Format(params, isTrue);
        case '81': return cond81Format(params, isTrue);
        case '82': return cond82Format(params, isTrue);
        case '83': return cond83Format(params, isTrue);
        case '86': return cond86Format(params, isTrue);
        case '87': return cond87Format(params, isTrue);
        case '89': return cond89Format(params, isTrue);
    }
    return null;
}

