function cond36Format(params, isTrue) {
    // 
    const variable = params[0];
    const op = params[1];
    const value = params[2];
    const [variableName, variableValue] = parseVariable(variable, value);
    const parsedOp = parseOp(op, isTrue);
    return `${variableName}${parsedOp}${variableValue}`
}

function parseOp(op, isTrue) {
    if (isTrue) {
        return op
    }
    switch (op) {
        case '==': return '!=';
        case '!=': return '==';
        case '>': return '<=';
        case '<': return '>=';
        case '>=': return '<';
        case '<=': return '>';
    }
    return null;
}

function parseVariable(variable, value) {
    // '-id-''-id'
    const reg = /(\w+)-(\d+)(?:-(\d+))?/;
    const match = reg.exec(variable);
    if (!match) {
        console.log('parseVariable error')
        return [];
    }
    const type = match[1];
    const id = match[2];
    const offset = match[3] || 0;
    // type=='team'
    if (type == 'team') {
        let valueText = ''
        if (value == '-1') {
            valueText = ''
        } else {
            valueText = parseIdText('0', value);
        }
        return [`${id}`, valueText]
    }


    const conf = searchRTypeConf(type, offset)
    const typeName = parseRType(type);
    const idText = parseIdText(type, id)
    const offsetName = conf ? conf.name : `${offset}`;
    const valueText = parseValueOfType(value, conf.type)

    return [`${typeName}.${idText}.${offsetName}`, valueText]
}

function parseRType(type) {
    switch (type) {
        case '0': return ''
        case '1': return ''
        case '2': return ''
        case '3': return ''
    }
    return null;
}

function parseIdText(type, id) {
    let name = '';
    switch (type) {
        case '0': name = searchHero(id)?.name; break;
        case '1': name = searchItem(id)?.name; break;
        case '3': name = searchSkill(id)?.name; break;
    }
    return `[${id}]${name}`
}

function parseValueOfType(value, type) {
    if (type == 'hero') {
        return parseIdText('0', value);
    }
    if (type == 'item') {
        return parseIdText('1', value);
    }
    if (type == 'skill') {
        return parseIdText('3', value);
    }
    return value;
}